import styles from './App.module.css';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import liff from '@line/liff';
import { buildReplyText } from 'line-message-builder';
import Home from "./components/pages/Home";
import Event from "./components/pages/Event";


function App() {
  const sendMessage = () => {
    liff.init({ liffId: process.env.REACT_APP_LIFF_ID }).then(() => {
      if (!liff.isLoggedIn()) {
        liff.login({})
      }
      const idToken = liff.getIDToken();
      alert(JSON.stringify(idToken, undefined, 2));
      const idTokenD = liff.getDecodedIDToken();
      alert(JSON.stringify(idTokenD, undefined, 2)); // print decoded idToken object
      const context = liff.getContext();
      alert(JSON.stringify(context, undefined, 2));
      liff.sendMessages(buildReplyText(['Send Message']))
    })
  }

  const getProfile = () => {
    liff.getProfile()
      .then(profile => {
        alert(JSON.stringify(profile, undefined, 2));
      })
      .catch((err) => {
        console.log('error', err);
      });
  }

  return (
    <Router>
      <div className="App">
        <button className="button" onClick={sendMessage}>
          Send Message
        </button>
        <button className="button" onClick={getProfile}>
          get ID Token
        </button>
        <nav className={styles.nav}>
          <ul className={styles.navUl}>
            <li className={styles.navLi}>
              <Link to="/">Home</Link>
            </li>
            <li className={styles.navLi}>
              <Link to="/event">Event</Link>
            </li>
          </ul>
        </nav>
      </div>
      <Switch>
        <Route path="/event">
          <Event />
        </Route>
        <Route path="/">
          <Home />
        </Route>
      </Switch>
    </Router>
  );
}

export default App;
